import React, { useEffect, useState } from "react";
import { IconButton } from "@material-ui/core";
import PrintIcon from "@material-ui/icons/Print";
import { jsPDF } from "jspdf";

import Alogo from "../images/Avantelogo.jpg";
import Footer from "../images/foot-back.png";
import { getAllProcessProdct } from "../Data/ProductsData";

export default function DownloadPDF({ data }) {
  const [pData, setPData] = useState([]);

  const getData = async () => {
    let da = await getAllProcessProdct().then((data) => data);

    setPData(da.message);
  };
  useEffect(() => {
    getData();
    return () => {
      console.log("Clean Up!");
    };
  }, []);
  var today = new Date();
  var dd = String(today.getDate()).padStart(2, "0");
  var mm = String(today.getMonth() + 1).padStart(2, "0");
  var yyyy = today.getFullYear();
  today = mm + "/" + dd + "/" + yyyy;
  const downloadAsPDFN = () => {
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, "0");
    var mm = String(today.getMonth() + 1).padStart(2, "0");
    var yyyy = today.getFullYear();
    today = mm + "/" + dd + "/" + yyyy;
    const CLogo =
      "data:image/jpeg;base64,";
    today = mm + "/" + dd + "/" + yyyy;
    const PDF = new jsPDF();
    PDF.setFont("courier", "normal");
    PDF.setFontSize(12);
    PDF.addImage(CLogo, "JPG", 10, 10, 50, 15);
    // PDF.text("Company Name", 105, 16, null, null, "center");
    // PDF.text("Address1", 105, 21, null, null, "center");
    // PDF.text("Address2", 105, 26, null, null, "center");
    // PDF.text("Address3", 105, 31, null, null, "center");
    // PDF.text("Address3", 105, 31, null, null, "center");
    PDF.text("Product Info", 180, 15, null, null, "center");

    PDF.line(202, 20, 155, 20);
    PDF.text(`Date: ${today}`, 200, 26, null, null, "right");
    PDF.line(202, 28, 155, 28);

    PDF.line(202, 30, 155, 30);
    PDF.text(`No: ${"          "}`, 200, 36, null, null, "right");
    PDF.line(202, 38, 155, 38);

    PDF.line(250, 43, 0, 43);
    PDF.text("Description", 57, 52, null, null, "center");
    PDF.text("Quantity", 133, 52, null, null, "center");
    PDF.line(120, 43, 120, (13.1 + 1.29) * 14);
    PDF.text("Price", 165, 52, null, null, "center");
    PDF.line(145, 43, 145, (13.1 + 1.29) * 14);
    PDF.text("Amount", 190, 52, null, null, "center");
    PDF.line(250, 55, 0, 55);
    PDF.line(179, 43, 179, 220);
    PDF.line(25, 43, 25, 220);
    PDF.text(`${data["PPN"]}`, 30, 61, null, null, "left");
    PDF.line(179, 63, 25, 63);
    data.PPL.forEach((pdfda, index) => {
      PDF.text(`${index + 1}`, 16, 70 + index * 10, null, null, "left");
      PDF.text(`${pdfda["RPN"]}`, 27, 70 + index * 10, null, null, "left");
      PDF.text(`${pdfda["RPQ"]}`, 130, 70 + index * 10, null, null, "left");
      PDF.text(`AED ${pdfda["RPP"]}`, 151, 70 + index * 10, null, null, "left");
      PDF.text(`AED ${pdfda["RPC"]}`, 182, 70 + index * 10, null, null, "left");
    });

    PDF.line(220, 13 * 15.5, 25, 13 * 15.5);
    PDF.text("Total", 160, (13 + 2) * 14, null, null, "center");
    PDF.text(`AED ${data["PPP"]}`, 192, (13 + 2) * 14, null, null, "center");
    PDF.line(250, 220, 0, 220);
    PDF.addImage(Footer, "PNG", 0, 275, 250, 25);
    PDF.save(`Avante_${today}.pdf`);
  };

  return (
    <>
      <IconButton onClick={downloadAsPDFN}>
        <PrintIcon />
      </IconButton>
      {/* <div style={{ marginTop: "100px", color: "red" }}>HanU</div> */}
      <div
        style={{ marginTop: "50px", padding: "20px", display: "none" }}
        id="PDFConti"
      >
        <div className="pdf-main">
          <section className="pdf-head">
            <div>
              <img src={Alogo} alt="logo" width={150} />
            </div>
            <div>
              <div>
                <h6>Product Info</h6>
              </div>
              <div>
                <span>Date:</span>
                <span>{today}</span>
              </div>
              <div>
                <span>No.</span>
                <span>Rand Number</span>
              </div>
            </div>
          </section>
          <section className="pdf-body">
            <table border="1">
              <thead>
                <tr>
                  <th style={{ width: "30px" }}>Sl No</th>
                  <th>Description</th>
                  <th style={{ width: "40px" }}>Quantity</th>
                  <th style={{ width: "100px" }}>Price</th>
                  <th style={{ width: "100px" }}>Amount</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td></td>
                  <td colSpan={4}>{data.PPD}</td>
                </tr>
                {console.log(data)}
                {data &&
                  data.PPL.map((pdfda, index) => (
                    <tr>
                      <td>{index + 1}</td>
                      <td>{pdfda.RPN}</td>
                      <td>{pdfda.RPQ}</td>
                      <td>AED {pdfda.RPP}</td>
                      <td>AED {pdfda.RPC}</td>
                    </tr>
                  ))}
                <tr>
                  <td></td>
                  <td colSpan={3}>Total</td>
                  <td>AED {data.PPP}</td>
                </tr>
              </tbody>
            </table>
          </section>
          <section className="pdf-footer">
            <div>
              <img src={Footer} alt="" width={"100%"} />
            </div>
          </section>
        </div>
      </div>
    </>
  );
}
