import React, { useState, useEffect } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast, ToastContainer, Slide } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { getAllRawProduct } from "../../Data/ProductsData";
import {
  Box,
  Container,
  Grid,
  Typography,
  Paper,
  Divider,
  TextField,
  Button,
  Icon,
  Chip,
  ButtonGroup,
  Switch,
  MenuItem,
  Radio,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import AddIcon from "@material-ui/icons/Add";
import RotateLeftIcon from "@material-ui/icons/RotateLeft";
import {
  createTheme,
  makeStyles,
  ThemeProvider,
} from "@material-ui/core/styles";
import {
  deepOrange,
  green,
  lightGreen,
  orange,
  purple,
  red,
  teal,
  yellow,
} from "@material-ui/core/colors";
import RPPiChart from "../Charts/RPCharts/RPPiChart";

const OwnStyles = makeStyles((theam) => ({
  successBtn: {
    borderColor: theam.palette.success.dark,
    color: theam.palette.success.dark,
  },
  errorBtn: {
    borderColor: theam.palette.error.dark,
    color: theam.palette.error.dark,
  },
  infoBtn: {
    borderColor: theam.palette.info.dark,
    color: theam.palette.info.dark,
  },
  ClipCount: {
    color: "#fff",
    fontSize: theam.typography.caption.fontSize,
    backgroundColor: theam.palette.info.main,
    paddingLeft: 1,
  },
  textRow: {
    display: "flex",
  },
  infoChip: {
    backgroundColor: teal[100],
    color: teal[900],
    border: `1px solid ${teal[900]}`,
    fontWeight: 700,
  },
}));

let api = axios.create({
  baseURL: `/`,
});

const AntSwitch = withStyles((theme) => ({
  root: {
    width: 28,
    height: 16,
    padding: 0,
    display: "inline-flex",
  },
  switchBase: {
    padding: 2,
    color: theme.palette.grey[500],
    "&$checked": {
      transform: "translateX(12px)",
      color: theme.palette.common.white,
      "& + $track": {
        opacity: 1,
        backgroundColor: theme.palette.primary.main,
        borderColor: theme.palette.primary.main,
      },
    },
  },
  thumb: {
    width: 12,
    height: 12,
    boxShadow: "none",
  },
  track: {
    border: `1px solid ${theme.palette.grey[500]}`,
    borderRadius: 16 / 2,
    opacity: 1,
    backgroundColor: theme.palette.common.white,
  },
  checked: {},
}))(Switch);

export default function RPViewAEM() {
  const classes = OwnStyles();
  let InnerWidth = () => window.innerWidth;
  const [Dimen, setDimen] = useState("mm");
  const [ssM, setSsM] = useState(false);
  const [checkBtnD, setCheckBtnD] = useState(true);
  const [checkWidth, setCheckWidth] = useState(InnerWidth);
  const [enteredData, setEnteredData] = useState({
    RPN: "",
    RPD: "",
    RPQ: "",
    RPP: "",
    RPW: "",
  });

  function getEnteredData(e) {
    let Name = e.target.name;
    let Value = e.target.value;
    setEnteredData((prevState) => ({
      ...prevState,
      [Name]: Value,
    }));
    let name = "";
    let dim1 = 0;
    let thi = 0;
    if (Name === "RPN") {
      name = Value;
    } else if (Name === "RPD") {
      dim1 = Value.toLowerCase().split("x")[0];
      // dim2 = Value.toLowerCase().split("x")[1];
    } else if (Name === "RPT") {
      thi = Value;
    }

    calcWeight(name, dim1, thi);
    checkBtn(name, dim1, thi);
  }
  const checkBtn = (name, dim, thi) => {
    console.log(name);
    console.log(dim);
    console.log(thi);
    if (name !== undefined && dim !== undefined && thi !== "") {
      setCheckBtnD(false);
    }
  };
  const calcWeight = (name, dim1, thi) => {
    // if (
    //   name.split(" ")[0].toLowerCase() == "ss" ||
    //   name.split(" ")[0].toLowerCase() == "stainless"
    // ) {
    //   if (
    //     name.split(" ")[1].toLowerCase() == "sheet" ||
    //     name.split(" ")[1].toLowerCase() == "material" ||
    //     name.split(" ")[2].toLowerCase() == "sheet" ||
    //     name.split(" ")[2].toLowerCase() == "material"
    //   ) {
    //     console.log(name.toLowerCase());
    //   }
    // }
    if (
      name.toLowerCase().startsWith("ss sheet") ||
      name.toLowerCase().startsWith("ss material")
    ) {
      // let calc = parseInt(dim1) * parseInt(dim2) * parseInt(thi) * 8;
      // console.log(calc);
    }
  };

  const checkSSMaterial = () => {
    setSsM((prev) => !prev);
  };

  const [rProduct, setRProduct] = useState([]);

  const getRPData = async () => {
    let da = await getAllRawProduct().then((data) => data);

    setRProduct(da.message);
  };

  const { state } = useLocation();
  let navigate = useNavigate();

  useEffect(() => {
    getRPData();

    const Data = state.Data.data;
    setEnteredData({
      RPN: Data["RPN"],
      RPD: Data["RPD"],
      RPQ: Data["RPQ"],
      RPP: Data["RPP"],
      RPW: Data["RPW"],
      id: Data["id"],
    });
    return () => {
      console.log("Cleaned Up!");
    };
  }, []);

  const reset = () => {
    setEnteredData({
      RPN: "",
      RPD: "",
      RPQ: "",
      RPP: "",
      RPW: "",
    });
  };

  const changeDimen = () => {
    Dimen === "mm" ? setDimen("mtr") : setDimen("mm");
    if (Dimen !== "mm") {
      // console.log("Now mm");
    } else {
      // console.log("Now mtr");
    }
  };

  const thik = [
    {
      value: "",
      label: "",
    },
    {
      value: "0.8",
      label: "0.8mm",
    },
    {
      value: "1",
      label: "1mm",
    },
    {
      value: "1.2",
      label: "1.2mm",
    },
    {
      value: "1.5",
      label: "1.5mm",
    },
    {
      value: "2",
      label: "2mm",
    },
  ];

  const createRawProduct = async () => {
    console.log(enteredData);
    // const response = await api.post(`anrp`, enteredData);

    // if (response.data.code.split("|")[0] === "Recover") {
    //   const btn = document.createElement("button");
    //   // btn.setAttribute("textContext", "Recover");
    //   btn.textContent = "Recover";
    //   const dun = recoverRawProduct;
    //   btn.addEventListener("click", function () {
    //     dun(response.data.code.split("|")[1]);
    //     // this.recoverRawProductTest(response.data.code.split("|")[1]);
    //   });
    //   document.querySelector("#btns").appendChild(btn);
    // }
    getRPData();
    toast.success("Raw Product Created in the system!", {
      position: "top-right",
      autoClose: 3000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "colored",
      delay: 500,
      transition: Slide,
    });
    reset();
  };

  const updateRawProduct = async () => {
    await api.put(`urp/${enteredData["id"]}`, enteredData);
    getRPData();
    // setEditID(editID);
    // reset();
    // this.setState({
    //   enteredData: { Id: "", RPN: "", RPD: "", RPQ: "", RPP: "", RPW: "" },
    //   editID: "",
    // });
    toast.info("Changes has been made to Raw Product", {
      position: "top-right",
      autoClose: 3000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "colored",
      transition: Slide,
    });
    // setTimeout(() => {
    //   navigate("/supp");
    // }, 1600);

    const Element = document.querySelector(".Toastify");
    const config = { childList: true, attributes: true };
    const callBack = (ML, OB) => {
      for (const m of ML) {
        if (m.type === "childList" && m.addedNodes.length === 0) {
          navigate("/rp");
        }
      }
    };
    const Obser = new MutationObserver(callBack);

    Obser.observe(Element, config);
  };

  const recoverRawProduct = async (_id) => {
    await api.put(`rrp/${_id}`);
    getRPData();
    reset();
  };

  const goBackRP = () => navigate("/rp");

  window.addEventListener("resize", function () {
    setCheckWidth(InnerWidth);
  });
  return (
    <>
      <Container>
        <div className="CheckHeader">
          {/* <Container> */}
          <Grid container>
            <Grid item xs={12} lg={5} md={5} xl={5} sm={7}>
              <Box>
                <Paper>
                  <Box
                    className={classes.textRow}
                    justifyContent="space-between"
                    alignItems="center"
                  >
                    <Typography variant="h5" component={"h6"}>
                      {state.Data["info"][0]}
                    </Typography>
                    {!state.Data["info"][3] ? (
                      <Chip
                        className={classes.infoChip}
                        label={`View: ${rProduct.length}`}
                        size="small"
                        clickable={true}
                        onClick={goBackRP}
                        variant="outlined"
                      />
                    ) : (
                      <Chip
                        className={classes.infoChip}
                        label={`Edit for: ${enteredData["RPN"]}`}
                        size="small"
                        clickable={true}
                        onClick={goBackRP}
                        variant="outlined"
                      />
                    )}
                  </Box>
                  <Box>
                    <Typography variant="body2" component={"p"}>
                      {state.Data["info"][1].split("|")[0]}
                    </Typography>
                    <Typography variant="body2" component={"p"} color="error">
                      <strong>{state.Data["info"][1].split("|")[1]}</strong>
                    </Typography>
                  </Box>
                  <Divider />
                  <Box component={"span"} style={{ float: "right" }}>
                    SS Material
                    <Switch onChange={checkSSMaterial} checked={ssM} />
                    {/* <AntSwitch onChange={checkSSMaterial} checked={ssM} /> */}
                  </Box>
                  {!ssM ? (
                    <Box padding={0.67}>
                      <TextField
                        id="outlined-basic"
                        label="Name"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPN"
                        value={enteredData["RPN"]}
                        onChange={getEnteredData}
                        size="medium"
                      />
                      <TextField
                        id="outlined-basic"
                        label="Dimension"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPD"
                        value={enteredData["RPD"]}
                        onChange={getEnteredData}
                      />
                      <TextField
                        id="outlined-basic"
                        label="Weight"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPW"
                        type="text"
                        value={enteredData["RPW"]}
                        onChange={getEnteredData}
                      />
                      <TextField
                        id="outlined-basic"
                        label="Quantity"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPQ"
                        type="number"
                        value={enteredData["RPQ"]}
                        onChange={getEnteredData}
                      />
                      <TextField
                        id="outlined-basic"
                        label="Price"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPP"
                        type="number"
                        value={enteredData["RPP"]}
                        onChange={getEnteredData}
                      />
                    </Box>
                  ) : (
                    <Box padding={0.67}>
                      <TextField
                        id="outlined-basic"
                        label="Name"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPN"
                        value={enteredData["RPN"]}
                        onChange={getEnteredData}
                        size="medium"
                      />

                      <Grid container>
                        <Grid item xs={3} lg={3} md={3} xl={3} sm={3}>
                          <TextField
                            id="outlined-basic"
                            label="Length"
                            type={"number"}
                            required
                            autoComplete="off"
                            variant="outlined"
                            fullWidth
                            margin="dense"
                            name="RPD"
                            value={enteredData["RPD"]}
                            onChange={getEnteredData}
                          />
                        </Grid>
                        <Grid item xs={3} lg={3} md={3} xl={3} sm={3}>
                          <TextField
                            id="outlined-basic"
                            label="Width"
                            type={"number"}
                            required
                            autoComplete="off"
                            variant="outlined"
                            fullWidth
                            margin="dense"
                            name="RPD"
                            value={enteredData["RPD"]}
                            onChange={getEnteredData}
                          />
                        </Grid>
                        <Grid item xs={4} lg={4} md={4} xl={4} sm={4}>
                          <TextField
                            id="outlined-basic"
                            label="Thickness"
                            type={"number"}
                            required
                            autoComplete="off"
                            variant="outlined"
                            fullWidth
                            margin="dense"
                            name="RPD"
                            value={enteredData["RPD"]}
                            onChange={getEnteredData}
                            select
                          >
                            {thik.map((th) => (
                              <MenuItem key={th.value} value={th.value}>
                                {th.label}
                              </MenuItem>
                            ))}
                          </TextField>
                        </Grid>
                        <Grid
                          item
                          xs={2}
                          lg={2}
                          md={2}
                          xl={2}
                          sm={2}
                          style={{
                            display: "inline-flex",
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                        >
                          {/* <TextField
                            id="outlined-basic"
                            label="Unit"
                            type={"number"}
                            required
                            autoComplete="off"
                            variant="outlined"
                            fullWidth
                            margin="dense"
                            name="RPD"
                            value={enteredData["RPD"]}
                            onChange={getEnteredData}
                          /> */}
                          <Box
                            component={"span"}
                            style={{
                              float: "right",
                              display: "inline-flex",
                              alignItems: "center",
                              flexDirection: "column",
                            }}
                            justifyItems={"center"}
                          >
                            <Box component={"h6"} margin={0}>
                              {Dimen}
                            </Box>
                            {/* <Box component={"span"} margin={1}></Box> */}
                            <AntSwitch onChange={changeDimen} />
                            {/* <Radio size="small" name="unit" /> */}
                            {/* <Radio size="small" name="unit" /> */}
                          </Box>
                        </Grid>
                      </Grid>
                      {/* <TextField
                        id="outlined-basic"
                        label="Dimension"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPD"
                        value={enteredData["RPD"]}
                        onChange={getEnteredData}
                      /> */}
                      {/* <TextField
                        id="outlined-basic"
                        label="Thickness"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPT"
                        value={enteredData["RPT"]}
                        onChange={getEnteredData}
                        select
                      >
                        {thik.map((th) => (
                          <MenuItem key={th.value} value={th.value}>
                            {th.label}
                          </MenuItem>
                        ))}
                      </TextField> */}
                      <Box component={"span"} style={{ float: "right" }}>
                        {/* <AntSwitch onChange={changeDimen} />
                        <Box component={"span"}>{Dimen}</Box> */}
                        {/* <Button variant="contained">Cal Weight</Button> */}
                        <Chip
                          label={"Cal Weight"}
                          clickable
                          size="small"
                          color="secondary"
                          disabled={checkBtnD}
                        />
                      </Box>
                      <TextField
                        id="outlined-basic"
                        label="Weight"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPW"
                        type="text"
                        value={enteredData["RPW"]}
                        onChange={getEnteredData}
                      />
                      <TextField
                        id="outlined-basic"
                        label="Quantity"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPQ"
                        type="number"
                        value={enteredData["RPQ"]}
                        onChange={getEnteredData}
                      />
                      <TextField
                        id="outlined-basic"
                        label="Price"
                        required
                        autoComplete="off"
                        variant="outlined"
                        fullWidth
                        margin="dense"
                        name="RPP"
                        type="number"
                        value={enteredData["RPP"]}
                        onChange={getEnteredData}
                      />
                    </Box>
                  )}

                  {ssM ? (
                    ""
                  ) : !state.Data["info"][3] ? (
                    <Box
                      className={classes.textRow}
                      justifyContent="space-between"
                      alignItems="center"
                      padding={0.67}
                    >
                      <Button
                        className={classes.successBtn}
                        variant="outlined"
                        size="medium"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        type="submit"
                        onClick={createRawProduct}
                      >
                        Create Raw Product
                      </Button>
                      <Button
                        className={classes.errorBtn}
                        variant="outlined"
                        size="medium"
                        variant="outlined"
                        startIcon={<RotateLeftIcon />}
                        onClick={reset}
                      >
                        Reset
                      </Button>
                    </Box>
                  ) : (
                    <Box
                      className={classes.textRow}
                      justifyContent="center"
                      alignItems="center"
                    >
                      <Button
                        className={classes.infoBtn}
                        variant="outlined"
                        size="medium"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        type="submit"
                        onClick={updateRawProduct}
                      >
                        Update Raw Product
                      </Button>
                    </Box>
                  )}
                </Paper>
              </Box>
            </Grid>
            {checkWidth > 768 && (
              <>
                <Grid item xs={0} lg={2} md={1} xl={2} sm={0}></Grid>
                <Grid item xs={12} lg={5} md={6} xl={5} sm={7}>
                  <RPPiChart />
                </Grid>
              </>
            )}
          </Grid>
          {/* </Container> */}
        </div>
      </Container>

      <ToastContainer />
    </>
  );
}
